* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.list {
  list-style: none;
}

.country-item {
  display: flex;
  align-items: center;
  gap: 5px;
}

.country-top-part {
  display: flex;
  flex-direction: row;
  gap: 5px;
  align-items: center;
}

.country-bot-part {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
